import request from '../../util/request.js'

export default {
	data() {
		return {
			pageName: 'index',
			data: null,
			articles: [],
			isLoad: false
		}
	},
	onLoad() {
		this.isLoad = true
		request.post('/articles', {
			p: 1
		}).then(res => {
			console.log(res);
			this.data = res
			this.articles = res.dtolist
			this.isLoad = false
		})

	},
	onReachBottom() {
		const {
			pager
		} = this.data;
		console.log(pager);
		if (pager.current < pager.pages && !this.isLoad) {
			//还存在下一页
			this.isLoad = true
			request.post('/articles', {
				p: pager.current + 1
			}).then(res => {
				console.log(res);
				this.data = res
				this.articles = this.articles.concat(res.dtolist);
				this.isLoad = false
			})
		} 
	},
	methods: {
		
	}
}
